.console {
  position: relative;
  margin: 23px;
  width: 300px;
  height: 400px;
  justify-content: center;
  padding: 10px;
  background-color: honeydew;
  border-radius: 10px;
  transition: 0.3s all;
  &:hover {
    content: '';
    margin-top: 10px;
    box-shadow: 19px 19px 19px 19px rgba(0, 0, 0, 0.25);
  }
  img {
    width: 100%;
  }
  &__title {
    display: flex;
    min-height: 48px;
    justify-content: center;
    margin: 5px 10px 13px 0;
    font-size: 23px;
  }
  h3 {
    display: flex;
    justify-content: center;

  }
}